import "./customized.scss";
import { useState } from 'react';
import Select from "react-select";


function Customized({addToCart}){
    const phoneColor=[
        {value:"i16pro-gold",label:"", color: "#d6c3b6"},
        {value:"i16pro-titanium",label:"", className:"i16pro-titanium"},
        {value:"i16pro-black",label:"", className:"i16pro-black"},
        {value:"i16pro-white",label:"", className:"i16pro-white"},
    ]
    const lensring=[
        {value:"lensring-titanium",label:""},
        {value:"lensring-gold",label:""},
        {value:"lensring-black",label:""},
        {value:"lensring-blue",label:""},
        {value:"lensring-iris",label:""}
    ]
    const sideButton=[
        {value:"button-titanium",label:""},
        {value:"button-gold",label:""},
        {value:"button-black",label:""},
        {value:"button-blue",label:""},
        {value:"button-purple",label:""},
        {value:"button-pink",label:""}
        
    ]
    const magsafe=[
        {value:"00",label:""},
        {value:"magsafe",label:"(+ NT$50)"}
    ]
    const lanyard=[
        {value:"00",label:""},
        {value:"lanyard-black",label:"( + NT$150)"},
        {value:"lanyard-white",label:"( + NT$150)"},
    ]
    //react-select
    const [caseStyle, setCaseStyle] = useState({
        color: "i16pro-gold",
        lensRing: "lensring-gold",
        sideButton: "button-gold",
        magsafe: "00",
        lanyard: "00"
      });//
      const [caseDetail, setCaseDetail] = useState({
        lensRing: "",
        sideButton: "",
        magsafe: "",
        lanyard: "",
        price:1000
      });//

    const changePhoneColor=(e)=>{
        setCaseStyle({
            ...caseStyle,
            color:e.value
        })
    }
    const changeLensColor=(e)=>{
        setCaseStyle({
            ...caseStyle,
            lensRing:e.value
        })
        setCaseDetail({
            ...caseDetail,
            lensRing:e.label
        })
    } 
    const changeButtonColor=(e)=>{
        setCaseStyle({
            ...caseStyle,
            sideButton:e.value
        })
        setCaseDetail({
            ...caseDetail,
            sideButton:e.label
        })
    } 
    const withmagsafe=(e)=>{
        setCaseStyle({
            ...caseStyle,
            magsafe:e.value
        })
        const newMagsafe=e.label
        if (newMagsafe!==""&&caseDetail.magsafe === ""){
            setCaseDetail({
                ...caseDetail,
                magsafe:newMagsafe,
                price:caseDetail.price + 50
        })//+50
        }else if(newMagsafe===""&&caseDetail.magsafe !== ""){
            setCaseDetail({
                ...caseDetail,
                magsafe:newMagsafe,
                price:caseDetail.price - 50
        })//
        }else{
            setCaseDetail({
                ...caseDetail,
                magsafe:newMagsafe
        }) 
        }
    }
    const withlanyard=(e)=>{
        setCaseStyle({
            ...caseStyle,
            lanyard:e.value
        })
        const newlanyard=e.label
        if (newlanyard!==""&&caseDetail.lanyard === ""){
            setCaseDetail({
                ...caseDetail,
                lanyard:newlanyard,
                price:caseDetail.price + 150
        })
        }else if(newlanyard===""&&caseDetail.lanyard !== ""){
            setCaseDetail({
                ...caseDetail,
                lanyard:newlanyard,
                price:caseDetail.price - 150
        })
        }else{
            setCaseDetail({
                ...caseDetail,
                lanyard:newlanyard
        }) 
        }
    }

//product id
    const generateProductId = (details) => {
        return `${details.lensRing}-${details.sideButton}-${details.magsafe}-${details.lanyard}`;
      };
    const product={
        _id:generateProductId(caseDetail),
        name:"",
        price:caseDetail.price,
        detail:caseDetail
    }

//
    const sendoder=()=>{
        console.log(product);
        alert("");
        window.location.reload();
        console.log("sendorder");
        addToCart(product);
        console.log("After addToCard");
    }
  
    return (
        <div className="custom">
            <div className="showProduct" >
                <img className="phone" alt="" src={require(`../../assets/photo/${caseStyle.color}.png`)}/>
                <img className="case" alt="" src={require('../../assets/photo/transparent.png')}/>
                <img className="lensRing" alt="" src={require(`../../assets/photo/${caseStyle.lensRing}.png`)}/>
                <img className="sideButton" alt="" src={require(`../../assets/photo/${caseStyle.sideButton}.png`)}/>
                <img className="magsafe" alt="" src={require(`../../assets/photo/${caseStyle.magsafe}.png`)}/>
                <img className="lanyard" alt="" src={require(`../../assets/photo/${caseStyle.lanyard}.png`)}/>
            </div>
            <div className="productSelect">
                <div className="optionTitle">:</div>
                {/* <select className="phoneColor" name="phoneColor" label="" onChange={changePhoneColor}>
                    <option value=""selected></option>
                    <option className="i16pro-gold" value="i16pro-gold"></option>
                    <option className="i16pro-titanium" value="i16pro-titanium" ></option>
                    <option className="i16pro-black" value="i16pro-black"></option>
                    <option className="i16pro-white" value="i16pro-white"></option>
                </select> */}
                <Select className="select" options={phoneColor} onChange={changePhoneColor} placeholder=""/>
                <div className="optionTitle">:</div>
                {/* <select className="lensRing" name="lensRing" onChange={changeLensColor}>
                    <option value="" selected></option>
                    <option value="lensring-titanium"></option>
                    <option value="lensring-gold" ></option>
                    <option value="lensring-black"></option>
                    <option value="lensring-blue"></option>
                    <option value="lensring-iris"></option>
                </select> */}
                <Select className="select" options={lensring} onChange={changeLensColor} placeholder=""/>
                <div className="optionTitle">:</div>
                {/* <select className="sideButton" name="sideButton" onChange={changeButtonColor}>
                    <option value="" selected></option>
                    <option value="button-titanium"></option>
                    <option value="button-gold" ></option>
                    <option value="button-black"></option>
                    <option value="button-blue"></option>
                    <option value="button-purple"></option>
                    <option value="button-pink"></option>
                </select> */}
                <Select className="select" options={sideButton} onChange={changeButtonColor} placeholder=""/>
                <div className="optionTitle">:</div>
                {/* <select className="magsafe" name="magsafe" onChange={withmagsafe}>
                    <option value="00" selected></option>
                    <option value="00"></option>
                    <option value="magsafe">(+ NT$50)</option>
                </select> */}
                <Select className="select" options={magsafe} onChange={withmagsafe} placeholder=""/>
                <div className="optionTitle">:</div>
                {/* <select className="lanyard" name="lanyard" onChange={withlanyard}>
                    <option value="00" selected></option>
                    <option value="00"></option>
                    <option value="lanyard-black">( + NT$150)</option>
                    <option value="lanyard-white">( + NT$150)</option>
                </select> */}
                <Select className="select" options={lanyard} onChange={withlanyard} placeholder=""/>
                <div className="productDetail">
                    <p></p>
                    <p>: Apple - iPhone 16 Pro</p>
                    <p>:{caseDetail.lensRing}</p>
                    <p>:{caseDetail.sideButton}</p>
                    <p>:{caseDetail.magsafe}</p>
                    <p>:{caseDetail.lanyard}</p>
                    <p>: {caseDetail.price}</p>
                </div>
                <button className="sendToCart" onClick={sendoder}></button>
            </div>
            
        
        </div>
    )
}





export default Customized;